<template>
  <div class="flex flex-col w-0 flex-1 overflow-y-auto">
    <nav class="bg-black z-20">
      <div class="mx-auto px-6">
        <div class="h-18 flex justify-between">
          <div class="flex flex-1 items-center">
            <div class="flex flex-1">
              <h1 class="text-white text-xl font-body topTitle">
                {{ currentTemplate }}
              </h1>
            </div>
            <div class="mr-2"></div>
            <div class="flex dropdown items-center justify-center ml-4">
              <div
                class="bg-primary h-10 w-10 rounded-full flex items-center justify-center"
              >
                <i class="fa fa-user-o topUser"></i>
              </div>

              <div class="mt-6 w-48 dropdown__menu right-0">
                <div>
                  <a class="dropdown__item" @click="toSettings">{{
                    $t("settings")
                  }}</a>
                  <a
                    class="dropdown__item"
                    data-testid="logout-action"
                    @click="logout"
                    >{{ $t("logout") }}</a
                  >
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </nav>
    <router-view></router-view>
    <div class="dashboardBtm flex justify-between shadow-lg">
      <div class="font-mono text-black text-sm">
        ©
        <!-- -->2021<!-- -->
        Apron Network
      </div>
      <div class="font-mono text-black text-sm">
        <span>{{ $t("supportBtm") }}: </span
        ><a href="http://rebase.network/" target="_blank" class="text-primary"
          >rebase</a
        >
      </div>
    </div>
  </div>
</template>

<script>
import layer from '@/utils/layer';
import api from '@/api';

export default {
  name: 'rightMain',
  data() {
    return {
      currentTemplate: this.$t('welcome'),
    };
  },
  watch: {
    $route(to) {
      if (to.path.indexOf('settings') > -1) {
        this.currentTemplate = this.$t('settings');
        // } else if (to.path.indexOf('heco') > -1 || to.path.indexOf('dashboard') > -1) {
      } else if (to.path.indexOf('heco') > -1) {
        this.currentTemplate = 'Heco';
      } else if (to.path.indexOf('bsc') > -1) {
        this.currentTemplate = 'BSC';
      } else {
        this.currentTemplate = this.$t('welcome');
      }
    },
  },
  methods: {
    toSettings() {
      this.$router.push({ path: '/dashboard/settings' });
    },
    logout() {
      api.loginApi.loginout().then((result) => {
        if (!result.success) {
          layer.tip(result.msg, 'error');
        } else {
          layer.tip(this.$t('loginoutsuccess'));
          localStorage.setItem('email', '');
          setTimeout(() => {
            this.$router.push('/loginRegister/login');
          }, 1000);
        }
      });
    },
  },
};
</script>

<style scoped lang="less">
.topTitle {
  text-transform: capitalize;
}
.topUser {
  color: #fff;
  font-size: 24px;
}
.dashboardBtm {
  padding: 10px;
  text-align: center;
}
</style>
